.top-container{
    display: flex;
    flex-direction: column;
    font-family: Oxygen, sans-serif;
    align-items: center;
    background-color: #2a3865;
    height: 100%;
    width: 100%;

    .title{
        color: #E4F4FB;        ;
        font-size: 19px;
        margin-top: 6px;
        margin-left: 14px;
    }
}

.weather-container{
    color: #fff;
    margin-top: 1.2em;
    .header{
        display: flex;
        // Para alinear horizontalmente
        flex-direction: row;
        justify-content: center;
        font-size: 20px;
    }

    .inner-container{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        color: #fff;
        margin: 20px 0;

    .current-weather{
        height: fit-content;
        font-size: 28px;
    }


        .image{
            width: 3.8em;
            height: 3.8em;
            img{
                width: 100%;
                height: 100%;
            }
        }
    }

    .footer{
        display: flex;
        flex-direction: row;
        justify-content: center;
        font-size: 13.4px;
        margin-top: 28px;
    }
}

.btn-select-location{
    background-color: #49DCC2;
    font-size: 12px;
    font-family: Roboto, sans-serif;
    border-radius: 4px;
    color: #222;
    margin-top: 35px;
}

.btn-select-location-popup{
    background-color: #49DCC2;
    font-size: 12px;
    font-family: Roboto, sans-serif;
    border-radius: 4px;
    color: #222;
    margin-top: 7px;
}