*{
    box-sizing: border-box;
}
    
html, 
body, 
#root{
    // Con root lo alineamos verticalmente al centro
    width: 100%;
    height: 100%;    
    background: linear-gradient(180deg, rgb(86, 109, 151) 0%, rgba(58, 98, 104, 0.60) 100%), url("../resources/images/pexels.jpeg") center;
    background-size: cover;
}

.app-container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
    .main-container{
        min-width: 600px;
        height: 550px;
        display: flex;
        flex-direction: column;
        border: 1px solid rgb(255, 255, 255);
        
        // Separar las secciones del contenedor a la mitad con flex
        .top-section{
            flex: 0 0 50%;
        }

        .bottom-section{
            flex: 0 0 50%;
        }
    }
}

.btn{
    border: 1px solid #ddd;
    margin: 15px 0;
    cursor: pointer;

    &:focus{
        outline: none;
    }
}

.popup-container{
    position: absolute;
    will-change: transform;
    top: -60px;
    left: -29px;
    transform: translate3d(487px, 177px, 0px);
    width: 14em;
    height: 5.2em;
    display: flex;
    border-radius: 6px;
    background-color: rgba(194, 205, 241, 0.452);
    box-shadow: 0px 0px 5px 2px rgba(15, 15, 15, 0.2);
    font-size: 14px;
    color: #fff;

.form-container{
    padding: 5px;
    width: 100%;
    label{
        font-size: 13px;
    }
    input{
        border-radius: 3px;
        border: 1px solid rgba(15, 15, 15, 0.2);
        margin-top: 3px;
    }
}
}